import React from "react";
import classNames from "classnames/bind";
import styles from "./Question.module.scss";
import errorImg from "~/assets/images/error.png";
import { useContext, useEffect, useState } from "react";
import { HomeContext } from "~/context/HomeContext";
import { getStorage } from "~/utils/chrome";

const cx = classNames.bind(styles);

const dataTest = [
  {
    no: 1,
    text: "on m JSP sau s dng i tng n no  ly d liu t yu cu HTTP ?",
    img_src: {
      error: true,
    },
    options: ["*A. request", "B. session", "C. application", "D. response"],
    select: [0],
  },
  {
    no: 2,
    text: 'Trong th <c:forEach items="${myList}" var="item">, tham s no c s dng  t tn cho bin i din cho tng phn t trong danh sch khi duyt qua danh sch ?',
    options: ["A. value", "B. index", "*C. item", "D. items"],
    select: [2],
  },
  {
    no: 3,
    text: "Cho cc yu cu phi chc nng nh sau:\n\nSUPL-1: H thng s dng kin trc ng dng dch v siu nh (Microservice Architecture)\n\nSUPL-2: C s d liu ca h thng c bo qun trn server ca cng ty.\n\nHy cho bit chng thuc tiu ch cht lng no trong b chun ISO/IEC 9126? (chn 2 p n ng)",
    options: [
      "*A. Thi gian phn hi (Response time)",
      "B. Tnh d s dng (Ease of use)",
      "C. Tnh bo mt (Security)",
      "*D. Tnh bo tr (Maintainability)",
    ],
    select: [0, 3],
  },
];

const renderQuestionText = (text = "") => {
  return text
    .split("\n")
    .filter((line) => line.trim() !== "")
    .map((line, i) => (
      <p key={i} className={cx("question-text")}>
        {line}
      </p>
    ));
};

const renderQuestionImg = (img = "") => {
  return !img ? null : img?.error ? (
    <img className={cx("question-img")} src={errorImg} alt=" " onCopy={null} />
  ) : (
    <img className={cx("question-img")} src={img} alt=" " onCopy={null} />
  );
};

const renderOption = (option = [], select = []) => {
  const set = new Set(select);
  return option?.map((option, i) => {
    if (option.type === "img") {
      return (
        <img
          key={i}
          src={option?.src}
          className={cx("option", { choose: set.has(i) })}
        />
      );
    } else {
      return (
        <p key={i} className={cx("option", { choose: set.has(i) })}>
          {option}
        </p>
      );
    }
  });
};

function Question() {
  const [test, setTest] = useState([]);
  const { key, handleSuccess, handleError } = useContext(HomeContext);

  useEffect(() => {
    getStorage("test")
      .then((result) => {
        setTest(result);
        handleSuccess(" ti xong cu hi");
      })
      .catch((e) => {
        setTest(dataTest);
        handleError(e.message);
      });
  }, [key]);

  return (
    <div className={cx("list-question")}>
      {test
        ?.filter((question) => question !== null)
        ?.map((question, i) => (
          <div key={`question${i}`} className={cx("question")}>
            <div className={cx("wrapper")}>
              <p className={cx("label")}>Question {question?.no}</p>
              {renderQuestionText(question?.text)}
              {renderQuestionImg(question?.img_src)}
              <div className={cx("line")}></div>
              {renderOption(question?.options, question?.select)}
              <div className={cx("br")}>
                <br />
              </div>
            </div>
          </div>
        ))}
    </div>
  );
}

export default Question;
